<template>
    <div class="top_echarts">
        <div class="boxsha"></div>
        <div id="ttdiv" :style='{"transform":top3_tr}'>
            <div id="top3_d1"></div>
            <div id="top3_d2"></div>
            <div id="top3_d3"></div>
        </div>
        <div class="right" @click="y_right">》</div>
        <div class="left" @click="y_left">《</div>
    </div>
</template>
<script>
    import echarts from "../../Echarts/china"
    export default {
        data() {
            return {
                top3_tr: '',
                ss: 'jj',
                count: 0,
                a: -3.5,
                r_time: null,
                l_time: null,
            }
        },
        methods: {
            b(a) {
                this.tr = a
            },
            y_left() {
                if (this.l_time !== null) {
                    clearTimeout(this.l_time);
                }
                this.l_time = setTimeout(() => {
                    this.a = this.a === -18.1 ? -3.5 : this.a -= 7.3
                    this.top3_tr = `translateX(${this.a}rem)`;
                }, 200)

            },
            y_right() {
                if (this.r_time !== null) {
                    clearTimeout(this.r_time);
                }
                this.r_time = setTimeout(() => {
                    this.a = this.a >= -3.6 ? -18.1 : this.a += 7.3
                    this.top3_tr = `translateX(${this.a}rem)`;//0.1+0.**会等于0.*000000000000001
                }, 200)
            },
            top3_echars_data() {
                let arr_data = []//时间
                let arr_certain = []//累计确诊
                let arr_recure = []//治愈
                let arr_die = []//驾鹤西游
                for (let f = 0; f < 5; f++) {
                    arr_data.push(this.$store.state.timedata[f].date)
                    arr_certain.push(this.$store.state.timedata[f].certain)
                    arr_recure.push(this.$store.state.timedata[f].recure)
                    arr_die.push(this.$store.state.timedata[f].die)
                }
                echarts.top2_city("top3_d1", arr_data, arr_certain)
                echarts.top3_city("top3_d2", arr_data, arr_recure)
                echarts.top3_d("top3_d3", arr_data, arr_recure)
            }
        },
        activated() {
            this.set = setTimeout(() => {
                if (this.$store.state.time === 'cg') {
                    clearTimeout(this.set)
                    this.top3_echars_data();
                }
            }, 100)
        },
        mounted() {
            let da = ['2020-12-10', '2021-03-10', '2021-09-10', '2021-12-10', '2022-03-10', '2022-09-10']
            let t2 = [12090987, 219876543, 3190888292, 4189876976, 5189897676, 6008989888]
            let t3 = [9120909, 12198765, 21888292, 31876976, 49897676, 68989888]
            let t4 = [22090987, 5876543, 990888292, 1189876976, 2489897676, 3808989888]
            echarts.top2_city("top3_d1", da, t2)
            echarts.top3_city("top3_d2", da, t4)
            echarts.top3_d("top3_d3", da, t3)
        },
    }
</script>
<style scoped>
    .top_echarts {
        position: relative;
        width: 100%;
        height: 4rem;
    }

    #ttdiv {
        margin-left: 50%;
        transform: translateX(-3.5rem);
        overflow: hidden;
        width: 21.6rem;
        display: flex;
        justify-content: space-between;
        height: 3.6rem;
        transition: all 0.3s linear;
    }

    .boxsha {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        z-index: 0;
        width: 7rem;
        height: 3.6rem;
        box-shadow: 0 0 .2rem rgb(179, 173, 255);
    }

    #ttdiv>div {
        width: 7rem;
        height: 3.6rem;
        background-color: rgb(245, 248, 248, 0.9);
        overflow: hidden;

    }

    .top_echarts>.right,
    .top_echarts>.left {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(40%, -50%);
        z-index: 99;
        width: .46rem;
        height: .4rem;
        background-color: rgb(0, 0, 0, .3);
        border-radius: .6rem;
        line-height: .4rem;
        text-align: center;
        color: antiquewhite;
    }

    .top_echarts>.left {
        transform: translate(-40%, -50%);
        left: 0;
    }

    .top_echarts>.right:hover {
        cursor: pointer;
    }

    .top_echarts>.left:hover {
        cursor: pointer;
    }
</style>