<template>
    <div class="top2_heaeder">
        <header>
            <ul class="domestic1">
                <li>
                    <p>较昨日{{ff($store.state.domestic["currentConfirmedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic["currentConfirmedCount"])}}</strong>
                    <p>现存确诊</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic["suspectedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic["suspectedCount"])}}</strong>
                    <p>镜外新增</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic["seriousIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic["seriousCount"])}}</strong>
                    <p>现存无症状</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic["confirmedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic["confirmedCount"])}}</strong>
                    <p>累计确诊</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic["deadIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic["deadCount"])}}</strong>
                    <p>累计死亡</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic["curedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic["curedCount"])}}</strong>
                    <p>累计治愈</p>
                </li>
            </ul>
        </header>

    </div>
</template>
<script>
    export default {
        methods: {
            ff(val) {
                if (val === undefined) return ':待发布'
                return val < 0 ? val : "+" + val
            },
            toThousands(num) {
                if (num === undefined) return
                let numArr = num.toString().split("");
                let arr = [];
                let count = 0; //用于计数
                for (let i = numArr.length - 1; i >= 0; i--) {
                    count++;
                    arr.unshift(numArr[i]);
                    if (!(count % 3) && i !== 0) arr.unshift(",");
                }
                return arr.join("");
            }
        }

    }
</script>
