<template>
    <div class="tap3_data">
        <div class="tap3_div" :class="m">
            <div class="tap_d1" ref="tt">
                <div class="taphead" :class="rr">
                    <p>国家</p>
                    <p>累计确诊</p>
                    <p>现确诊</p>
                    <p>治愈</p>
                    <p>死亡</p>
                </div>
            </div>
            <div class="tab" ref="fa">
                <tab v-for="(val,index) in top3_world"
                    :valname="[val.areaName,val.confirmedNum,val.currentConfirmedNum,val.curedNum,val.deadNum]"
                    :arrcity="val.city" :key="index" :dn="'none'">
                </tab>

            </div>
            <div class="nav_right" v-if="dis">
                <span class="s1"></span>
                <span class="s2"></span>
                <span class="s3"></span>
                <span class="s4"></span>
                <span class="s5"></span>
                <p style="text-align:center">加载中...</p>
            </div>
        </div>
        <p class="look" @click="more">{{look_more}}</p>
    </div>
</template>
<script>
    import tab from "../top1/toplist"
    import axios from "axios"
    export default {
        data() {
            return {
                rr: '',
                m: '',
                look: '',
                look_more: '查看更多',
                top3_world: undefined,
                dis: true
            }
        },
        created() {
            axios({
                url: 'https://route.showapi.com/2217-5?showapi_appid=1171215&showapi_sign=fec3cf667d044f83ba8133ad5bb88a56',
            }).then(res => {
                this.top3_world = res.data.showapi_res_body.foreignList
                this.dis = false
            }, err => {
                alert("世界疫情数据获取失败请刷新重试");
            })
        },
        components: {
            tab
        },
        methods: {
            //滚动条事件
            res_ttTop(ele) {
                var top = ele.offsetTop;
                var part = ele.offsetParent;
                while (part !== null) {
                    top += part.offsetTop;
                    part = part.offsetParent;
                }
                return top;
            },
            scrool() {
                document.body.onscroll = () => {
                    let ttop = this.res_ttTop(this.$refs.tt);
                    if ((document.body.scrollTop || document.documentElement.scrollTop) > ttop) {
                        this.rr = "rr";
                    } else if ((document.body.scrollTop || document.documentElement.scrollTop) < ttop) {
                        this.rr = "";
                    }
                };
            },
            more() {
                this.m = this.m === '' ? "tab_m" : ''
                this.look_more = this.look_more === '查看更多' ? "收起" : '查看更多'
            }
            //滚动条事件结束
        },
        deactivated() {
            document.body.onscroll = null
        },
        mounted() {
            this.scrool()
        },
        activated() {
            this.scrool()
        },
        deactivated() {
            document.body.onscroll = null
        },

    }
</script>
<style scoped>
    .tap3_div {
        height: 16rem;
        overflow: hidden;
        position: relative;
    }

    .tap3_data {
        height: 60%;
        font-size: .2rem;
    }

    .taphead {
        width: 100%;
        display: table;
        height: .9rem;
    }

    .taphead>p {
        display: table-cell;
        font-weight: bold;
        width: .6rem;
        height: .4rem;
        text-align: center;
        vertical-align: middle;
    }

    .taphead>p+p {
        border-left: .001rem solid whitesmoke;
    }

    .taphead>p:nth-child(1) {
        background-color: rgb(166, 223, 233);
    }

    .taphead>p:nth-child(2) {
        background-color: rgb(233, 166, 213);
    }

    .taphead>p:nth-child(3) {
        background-color: rgb(243, 143, 143);
    }

    .taphead>p:nth-child(4) {
        background-color: rgb(170, 166, 233);
    }

    .taphead>p:nth-child(5) {
        background-color: rgb(180, 176, 156);
    }

    .rr {
        position: fixed;
        top: 0;
        left: 0;
        height: .6rem;
        z-index: 100;
    }

    .look {
        font-size: .14rem;
        text-align: center;
        line-height: .4rem;
        margin: 0 auto;
        width: 2rem;
        height: .4rem;
        color: rgb(73, 76, 255);
        border-radius: .2rem;
    }

    .look:hover {
        cursor: pointer;
    }

    .tab_m {
        width: 100%;
        height: 80%;
    }

    .tab {
        position: relative;

    }

    .nav_right {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1.8rem;
    }

    .nav_right>span {
        display: inline-block;
        margin-right: .2rem;
        width: .14rem;
        height: 1rem;
        background-color: rgb(241, 240, 240);
        border-radius: 50%/0.1rem;
    }

    .s1 {
        animation: atguigu 0.5s linear 0.1s infinite;
    }

    .s2 {
        animation: atguigu 0.5s linear 0.3s infinite;
    }

    .s3 {
        animation: atguigu 0.5s linear 0.6s infinite;
    }

    .s4 {
        animation: atguigu 0.5s linear 0.9s infinite;
    }

    .s5 {
        animation: atguigu 0.5s linear 1.3s infinite;
    }

    @keyframes atguigu {
        0% {
            transform: scaleY(1);
            background-color: aqua;
        }

        50% {
            transform: scaleY(.5);
            background-color: rgb(234, 0, 255);
        }

        100% {
            transform: scaleY(1);
            background-color: rgb(72, 255, 0);
        }

    }
</style>