<template>
    <div class="top2_heaeder">
        <header>
            <ul class="domestic1">
                <li>
                    <p>较昨日{{ff($store.state.domestic.foreignStatistics["currentConfirmedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic.foreignStatistics["currentConfirmedCount"])}}</strong>
                    <p>现存确诊</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic.foreignStatistics["confirmedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic.foreignStatistics["confirmedCount"])}}</strong>
                    <p>累计确诊</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic.foreignStatistics["deadIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic.foreignStatistics["deadCount"])}}</strong>
                    <p>累计死亡</p>
                </li>
                <li>
                    <p>较昨日{{ff($store.state.domestic.foreignStatistics["curedIncr"])}}</p>
                    <strong>{{toThousands($store.state.domestic.foreignStatistics["curedCount"])}}</strong>
                    <p>累计治愈</p>
                </li>
            </ul>
        </header>

    </div>
</template>
<script>
    export default {
        methods: {
            ff(val) {
                if (val === undefined) return ':待发布'
                return val < 0 ? val : "+" + val
            },
            toThousands(num) {
                if (num === undefined) return
                let numArr = num.toString().split("");
                let arr = [];
                let count = 0; //用于计数
                for (let i = numArr.length - 1; i >= 0; i--) {
                    count++;
                    arr.unshift(numArr[i]);
                    if (!(count % 3) && i !== 0) arr.unshift(",");
                }
                return arr.join("");
            }
        }

    }
</script>
<style scoped>
    .top2_heaeder>header {
