        background: linear-gradient(to bottom, rgb(206, 206, 206), rgb(74, 74, 75), rgb(206, 206, 206));
    }

    header>.domestic1>li:nth-child(2)::after,
    header>.domestic1>li:nth-child(3)::after {
        position: absolute;
        /* top: 50%; */
        right: 0;
        transform: translateX(-50%);
