            <h2>新冠预防指南</h2>
            <h3>（试行）</h3>
            <p>一、尽量减少外出，不要去人群密集的场所，避免与任何有感冒或者流感样症状的患者密切接触。</p>

            <p>二、外出、就医或者乘坐公共交通工具时，应该要佩戴医用外科口罩或者N95口罩。

            <p> 三、不要购买、接触或者食用野生动物和冷链食品。</p>

            <p> 四、应该注意手卫生，勤洗手，多使用洗手液、香皂以及流动的水洗手，也可以使用含有酒精的免洗洗手液进行消毒。

            <p>五、咳嗽或者打喷嚏应该用纸巾或者手肘遮住口鼻。</p>

            <p>六、注意多喝水、多休息、避免熬夜，适当运动，增强机体免疫力。</p>

            <p>七、保持住处通风，常备体温计、一次性口罩以及家庭消毒用品等。</p>

            <p>八、疑似新冠肺炎密切接触者、次密切接触者或疑似被感染者等高风险人群，除隔离观察外，可辅以中药预防治疗：藿香正气水，口服，1次10ml，1日两次；莲花清瘟颗粒，口服，1次一袋，1日三次，疗程7-10天；莲花清瘟胶囊，口服，1次四粒，1日三次，疗程7-10天。
            </p>
