<template>
    <div class="top_echarts">
        <div class="topipt">
            <div id="ttdiv">
                <select ref="ipt" :style="{height:add}" :size="size" @focus="sf" @blur="sf_m" @change="echarts_img"
                    v-model="text">
                    <option value="北京市">北京市</option>
                    <option value="广东省">广东省</option>
                    <option value="陕西省">陕西省</option>
                    <option value="广西壮族自治区">广西壮族自治区</option>
                    <option value="台湾">台湾</option>
                    <option value="浙江省">浙江省</option>
                    <option value="湖北省">湖北省</option>
                    <option value="河北省">河北省</option>
                    <option value="海南省">海南省</option>
                    <option value="山东省">山东省</option>
                    <option value="吉林省">吉林省</option>
                    <option value="贵州省">贵州省</option>
                    <option value="福建省">福建省</option>
                    <option value="安徽省">安徽省</option>
                    <option value="四川省">四川省</option>
                    <option value="重庆市">重庆市</option>
                    <option value="吉林省">吉林省</option>
                    <option value="江西省">江西省</option>
                    <option value="江苏省">江苏省</option>
                    <option value="上海市">上海市</option>
                    <option value="辽宁省">辽宁省</option>
                    <option value="内蒙古自治区">内蒙古自治区</option>
                    <option value="天津市">天津市</option>
                    <option value="宁夏回族自治区">宁夏省回族自治区</option>
                    <option value="澳门">澳门</option>
                    <option value="香港">香港</option>
                </select>
            </div>
        </div>
        <div ref="top_h">
            <div id='d1'></div>
        </div>
    </div>
</template>
<script>
    import ico from "../../Echarts/china"
    export default {
        data() {
            return {
                text: '北京市',
                size: '',
                add: "",
                ss: ''
            }
        },
        methods: {
            b(a) {
                this.tr = a
            },
            echarts_img() {
                var arr = undefined;
                let data = this.$store.state.yqname_list;
                for (let a = 0; a < data.length; a++) {
                    if (this.text.indexOf(data[a].provinceName) !== -1) {
                        this.ss = this.text
                        // this.sf_m()
                        arr = [data[a].confirmedNum, data[a].currentConfirmedNum, data[a].curedNum, data[a].deadNum]
                        ico.top2_echarts("d1", arr, this.ss);
                        // 去除焦点
                        this.$refs.ipt.blur()
                        return
                    }
                }
            },
            sf() {
                this.size = "6",
                    this.add = "4rem"
            },
            sf_m() {
                this.size = "",
                    this.add = "",
                    this.text = this.ss;
            },
            ll() {
                // 开始就显示
                let ti = setInterval(() => {
                    if (this.$store.state.time === 'cg1') {
                        clearInterval(ti)
                        this.echarts_img()
                    }
                }, 100)
            }
        },
        mounted() {
            this.ll()
        },
        // 激活时触发
        activated() {
            this.echarts_img()
        }
    }
</script>
<style scoped>
    .top_echarts {
        width: 100%;
        height: 4rem;
        /* background-color: rgb(255, 34, 34); */
    }

    .topipt {
        position: relative;
        height: .4rem;
    }

    #ttdiv {
        position: absolute;
        z-index: 9;
        width: 100%;
        height: 100%;
        background-color: rgb(255, 255, 255, 0);

    }

    .topipt>div>select {
        position: absolute;
        right: .1rem;
        line-height: .3rem;
        width: 2rem;
        height: .4rem;
        font-size: .2rem;
    }

    .topipt>div>select>option+option {
        margin: .2rem 0;
    }

    .top_echarts>div:nth-child(2) {
        margin-left: 50%;
        transform: translateX(-3.5rem);
        overflow: hidden;
        width: 28rem;
        display: flex;
        justify-content: space-between;
        height: 3.4rem;
        /* background-color: blueviolet; */
        transition: all 0.3s linear;
    }

    .top_echarts>div>div {
        width: 7rem;
        height: 100%;
    }

    .top_echarts>div>div:nth-child(1) {
        background-color: rgb(245, 248, 248, 0.9);
    }


    .top_echarts_buttom {
        display: flex;
        justify-content: space-around;
        width: 100%;
        height: .5rem;
    }

    .top_echarts_buttom>button {
        position: relative;
        bottom: -.1rem;
        color: rgb(116, 113, 113);
        width: 2rem;
        border: .01rem rgb(126, 126, 126) solid;
        height: .35rem;
        line-height: .35rem;
        /* background-color: blue; */
    }
</style>