<template>
    <div class="top1">
        <div class="top1_div1">
            <div class="top1_tit">
                <p>近期疫情分布
                    <span>截至：{{time}}</span>
                </p>
            </div>
            <div>
                高风险地区<span>{{$store.state.highDangerCount}}</span>个&nbsp&nbsp中风险地区<span>{{$store.state.midDangerCount}}</span>个
            </div>
            <div class="tap">
                <div class="tap_d1" ref="tt">
                    <div class="taphead" :class="flaot">
                        <p>地区</p>
                        <p>累计确诊</p>
                        <p>现确诊</p>
                        <p>治愈</p>
                        <p>死亡</p>
                    </div>
                </div>
                <toplist v-for="(val,index) in  yq_list" :key="index"
                    :valname="[val.provinceName,val.confirmedNum,val.currentConfirmedNum,val.curedNum,val.deadNum]"
                    :arrcity="val.cityList">
                </toplist>
                <p style="height:.6rem;line-height:.6rem;font-size:.2rem;color: rgb(121, 121, 121);">(数据有延迟,仅供参考)</p>
            </div>
            <hr style="width:100%;">
            <div class="chain1">
                <div class="top1_tit" ref="gn">
                    <p>国内疫情地图 </p>
                </div>
                <chainview></chainview>
            </div>
        </div>
    </div>
</template>
<script>
    import toplist from "../../components/top1/toplist"
    import chainview from "../../components/top1/chain_view"
    import { mapState } from "vuex"
    export default {
        name: 'top_disp',
        data() {
            return {
                gfx: "",
                zfx: "",
                flaot: '',
            }
        },
        computed: {
            ...mapState({ time: "times" }),
            yq_list() {
                return this.$store.state.yqname_list;
            },
        },
        components: {
            toplist,
            chainview,
        },
        methods: {
            res_ttTop(ele) {
                var top = ele.offsetTop;
                var part = ele.offsetParent;
                while (part !== null) {
                    top += part.offsetTop;
                    part = part.offsetParent;
                }
                return top;
            },
            scrool() {
                document.body.onscroll = () => {
                    let ttop = this.res_ttTop(this.$refs.tt);
                    let gn = this.res_ttTop(this.$refs.gn);
                    if ((document.body.scrollTop || document.documentElement.scrollTop) > ttop) {
                        this.flaot = "flaot";
                    } else if ((document.body.scrollTop || document.documentElement.scrollTop) < ttop) {
                        this.flaot = "";
                    } if ((document.body.scrollTop || document.documentElement.scrollTop) > gn) {
                        this.flaot = "";
                    }
                };
            }
        },
        mounted() {
            this.scrool()
        },
        beforeDestroy() {
            document.body.onscroll = null
        },
    }
</script>
<style scoped>
    .top1 {
        padding-top: .3rem;
        width: 100%;
        /* overflow: hidden; */
        box-sizing: border-box;
    }

    .top1_div1 {
        box-sizing: border-box;
        width: 96%;
        margin: 0 auto;
        font-size: .2rem;
    }

    .top1_tit {
        margin-bottom: .2rem;
    }

    .top1_tit>p:nth-child(1) {
        position: relative;
        padding-left: .2rem;
        border-left: 0.1rem blue solid;
        font-size: .3rem;
        font-weight: 600;
        color: rgb(32, 31, 31);
    }

    .top1_tit>p:nth-child(1)>span {
        position: absolute;
        right: 0;
        bottom: 0;
        font-size: .1rem;
        color: rgb(158, 161, 163);
    }

    .top1_div1>div:nth-child(2) {
        padding-left: .1rem;
        box-sizing: border-box;
        border: .01rem solid rgb(155, 153, 153);
        border-radius: .1rem;
        margin-bottom: .2rem;
        height: .8rem;
        line-height: .8rem;
    }

    .top1_div1>div:nth-child(2)>span {
        font-size: .3rem;
        font-weight: bold;
        margin: 0 .1rem;
    }

    .top1_div1>div:nth-child(2)>span:nth-child(1) {
        color: rgb(190, 0, 0);
    }

    .top1_div1>div:nth-child(2)>span:nth-child(2) {
        color: rgb(255, 174, 0);
    }

    .tap {
        width: 100%;
    }

    .tap_d1 {
        width: 100%;
        height: .9rem;
    }

    .taphead {
        width: 100%;
        display: table;
        height: .9rem;
    }

    /* 绝对定位 */
    .flaot {
        position: fixed;
        top: 0;
        left: 0;
        height: .6rem;
        z-index: 100;

    }

    .taphead>p {
        display: table-cell;
        font-weight: bold;
        width: .6rem;
        height: .4rem;
        text-align: center;
        vertical-align: middle;
    }

    .taphead>p+p {
        border-left: .001rem solid whitesmoke;
    }

    .taphead>p:nth-child(1) {
        background-color: rgb(166, 223, 233);
    }

    .taphead>p:nth-child(2) {
        background-color: rgb(233, 166, 213);
    }

    .taphead>p:nth-child(3) {
        background-color: rgb(243, 143, 143);
    }

    .taphead>p:nth-child(4) {
        background-color: rgb(170, 166, 233);
    }

    .taphead>p:nth-child(5) {
        background-color: rgb(180, 176, 156);
    }

    .chain1 {
        width: 100%;
    }

    .chain1_body {
        width: 100%;
        height: 5.5rem;
        background-color: rgb(81, 255, 0);
    }
</style>