<template>
    <div class="top2">
        <div class="top2_div1">
            <p class="times">截止于:{{this.$store.state.times}}</p>
            <!-- 路由组件失活时，它内部的一般组件也处于失活状态 -->
            <top2_header></top2_header>
            <div class="top_ipt">
                <p>各省数据图形</p>
            </div>
            <top2_echarts></top2_echarts>
            <div class="top_ipt">
                <p>详细数据如下</p>
            </div>
            <div>
                <div class="tap_d1" ref="tt">
                    <div class="taphead" :class="rr">
                        <p>地区</p>
                        <p>累计确诊</p>
                        <p>现确诊</p>
                        <p>治愈</p>
                        <p>死亡</p>
                    </div>
                </div>
                <div class="tab" :class="m">
                    <tab v-for="(val,index) in  yq_list" :key="index"
                        :valname="[val.provinceName,val.confirmedNum,val.currentConfirmedNum,val.curedNum,val.deadNum]"
                        :arrcity="val.cityList">
                    </tab>
                    <p @click="more" :class="look">{{look_more}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapState } from "vuex"
    import top2_header from "../../components/top2/top2_header"
    import top2_echarts from "../../components/top2/top2_echarts"
    import tab from '../../components/top1/toplist'
    export default {
        // data检测不到vuex数据的变化
        name: 'top2',
        data() {
            return {
                rr: '',
                m: '',
                look: '',
                look_more: '查看更多'
            }
        },
        components: {
            top2_header,
            top2_echarts,
            tab,
        },
        computed: {
            yq_list() {
                return this.$store.state.yqname_list;
            },
        },
        methods: {
            res_ttTop(ele) {
                var top = ele.offsetTop;
                var part = ele.offsetParent;
                while (part !== null) {
                    top += part.offsetTop;
                    part = part.offsetParent;
                }
                return top;
            },
            scrool() {
                document.body.onscroll = () => {
                    let ttop = this.res_ttTop(this.$refs.tt);
                    if ((document.body.scrollTop || document.documentElement.scrollTop) > ttop) {
                        this.rr = "rr";
                    } else if ((document.body.scrollTop || document.documentElement.scrollTop) < ttop) {
                        this.rr = "";
                    }
                };
            },
            more() {
                this.m = this.m === '' ? "tab_m" : ''
                this.look = this.look === '' ? "look" : ''
                this.look_more = this.look_more === '查看更多' ? "收起" : "查看更多"
            }
        },
        mounted() {
            this.scrool()
        },
        // 失活时触发(解决当该组件被失活时，滚动条还在执行导致失效和报错问题)
        deactivated() {
            document.body.onscroll = null
        },
        // 激活时触发
        activated() {
            this.scrool()
        },

    }
</script>
<style scoped>
    .times {
        text-align: right;
        margin: .2rem;
        font-size: .1rem;
        color: rgb(107, 107, 107);
    }


    .top2 {
        width: 100%;
        margin-bottom: 1rem;
        /* height: 26rem; */
        /* background-color: rgb(67, 107, 30); */
    }

    .top2_div1 {
        overflow: hidden;
        width: 96%;
        /* height: 26rem; */
        margin: 0 auto;
        font-size: .2rem;
        padding-top: .01rem;
        /* background-color: aquamarine; */
    }

    .top_ipt>p:nth-child(1) {
        margin: .2rem 0;
        position: relative;
        padding-left: .2rem;
        border-left: 0.1rem blue solid;
        font-size: .3rem;
        font-weight: 600;
        color: rgb(32, 31, 31);
    }

    .tab {
        position: relative;
        width: 100%;
        height: 10rem;
        /* background-color: burlywood; */
        overflow: hidden;
    }

    .tab>p {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: .6rem;
        text-align: center;
        line-height: .5rem;
        color: rgb(0, 81, 255);
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.9));
        border-radius: 20% 20% 0 0;
    }

    .tab>p:hover {
        cursor: pointer;
    }

    .tab_m {
        width: 100%;
        height: 100%;
    }

    .taphead {
        width: 100%;
        display: table;
        height: .9rem;
    }

    .taphead>p {
        display: table-cell;
        font-weight: bold;
        width: .6rem;
        height: .4rem;
        text-align: center;
        vertical-align: middle;
    }

    .taphead>p+p {
        border-left: .001rem solid whitesmoke;
    }

    .taphead>p:nth-child(1) {
        background-color: rgb(166, 223, 233);
    }

    .taphead>p:nth-child(2) {
        background-color: rgb(233, 166, 213);
    }

    .taphead>p:nth-child(3) {
        background-color: rgb(243, 143, 143);
    }

    .taphead>p:nth-child(4) {
        background-color: rgb(170, 166, 233);
    }

    .taphead>p:nth-child(5) {
        background-color: rgb(180, 176, 156);
    }

    .rr {
        position: fixed;
        top: 0;
        left: 0;
        height: .6rem;
        z-index: 100;
    }
</style>