<template>
    <div class="home">
        <div></div>
        <div class="news_yq">
            <p>
                疫情热点
            </p>
            <a v-for="val in journalism" :href="val.sourceUrl" :key="val.id" class="new">
                <span>
                    最新
                </span>
                {{val.title}}
                <span class="sp">
                    <span></span>
                    <span></span>
                </span>
            </a>
        </div>
        <hr class="hr">
        <div class="fingerpost">
            <router-link to="/guide">
                <figure>
                    <img src="../assets/imgs/ys.png" alt="">
                    <figcaption>防护指南</figcaption>
                </figure>
            </router-link>
            <router-link to="/navigation?tit='问医生'">
                <figure>
                    <img src="../assets/imgs/hs.png" alt="">
                    <figcaption>核酸查询</figcaption>
                </figure>
            </router-link>
            <router-link to="/supplies">
                <figure>
                    <img src="../assets/imgs/wz.png" alt="">
                    <figcaption>防疫物资</figcaption>
                </figure>
            </router-link>
            <router-link to="/phoe">
                <figure>
                    <img src="../assets/imgs/cx.png" alt="">
                    <figcaption>问题答疑</figcaption>
                </figure>
            </router-link>
        </div>
        <div>
            <router-view></router-view>
        </div>
        <footer class="foot">疫情之下无小事，打疫苗，勤洗手，常通风，不乱走，共筑防疫堡垒</footer>
    </div>
</template>
<script>
    import axios from 'axios'
    import virus from "../api/virus"
    export default {
        name: 'home',
        data() {
            return {
                journalism: [],
            }
        },
        created() {
            axios({
                url: virus.epidemicData,
            }).then(
                res => {
                    this.journalism.push(...res.data.newslist[0].news);//提取新闻
                    this.$store.commit("data_domestic", res.data.newslist[0].desc)    //国内现确诊，累计等数据
                    // 有问题
                    res.data.newslist[0].desc.highDangerCount;
                    res.data.newslist[0].desc.midDangerCount
                    this.$store.commit("high", [res.data.newslist[0].desc.highDangerCount, res.data.newslist[0].desc.midDangerCount]);//提取高低风险地区
                },
                error => {
                    alert('数据申请失败，请刷新重试');
                }
            );

        },

    }
</script>
<style scoped>
    .home {
        position: relative;
        width: 100%;
        height: 100%;
    }

    /* 头部图片 */
    .home>div:nth-child(1) {
        width: 100%;
        height: 2.5rem;
        background: url(../assets/imgs/tb.webp);
        background-size: 100% 3.5rem;
        background-position: -0.4rem -0.4rem;
    }

    /* 疫情热点 */
    .news_yq>p:nth-child(1) {
        margin: .4rem .2rem;
        padding-left: .2rem;
        border-left: 0.1rem blue solid;
        font-weight: 600;
        color: rgb(32, 31, 31);
    }

    /* 疫情热点新闻列表*/
    .news_yq>.new {
        position: relative;
        display: block;
        padding-left: .22rem;
        padding-right: .7rem;
        word-break: break-all;
        font-size: .26rem;
    }

    .news_yq>.new+a {
        margin-top: .3rem;
    }

    .news_yq>.new>span:nth-child(1) {
        display: inline-block;
        margin-right: .06rem;
        background: linear-gradient(to left, rgb(255, 141, 141), rgb(250, 149, 253));
        border-radius: .06rem;
        font-size: .16rem;
        padding: .06rem .1rem;
        color: whitesmoke;
    }

    .news_yq>.new>.sp {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: .2rem;
        font-weight: bold;
        color: rgb(109, 107, 107);
    }

    .news_yq>.new>.sp>span {
        display: block;
        width: .15rem;
        height: .02rem;
        background-color: rgb(153, 150, 150);
    }

    .news_yq>.new>.sp>span:nth-child(1) {
        transform: rotate(-35deg) translateY(.083rem);
    }

    .news_yq>.new>.sp>span:nth-child(2) {
        transform: rotate(35deg) translateY(-.083rem);
    }

    .hr {
        width: 90%;
        margin: .4rem auto;
    }

    /* 指南列表 */
    .fingerpost {
        display: table;
        width: 100%;
        height: 1.8rem;
        text-align: center;
        margin-top: .6rem;
    }

    .fingerpost:hover {
        cursor: pointer;
    }

    .fingerpost>a {
        display: table-cell;
    }

    .fingerpost img {
        width: 1.2rem;
        height: 1.2rem;
    }

    .fingerpost figcaption {
        position: relative;
        top: -0.2rem;
        font-size: .2rem;
    }

    /* 底部 */
    .foot {
        text-align: center;
        font-size: .2rem;
        line-height: .4rem;
        width: 100%;
        height: .4rem;
        background-image: -webkit-linear-gradient(bottom, rgb(255, 253, 115), rgb(250, 162, 0), rgb(240, 100, 36), rgb(252, 0, 0));
        background-clip: text;
        -webkit-text-fill-color: transparent;
        border: .01rem solid rgb(197, 197, 197);
    }
</style>