import "./china1"
import * as echarts from 'echarts/core';
import { TitleComponent, TooltipComponent, GridComponent, DatasetComponent, TransformComponent } from 'echarts/components';
import { ScatterChart, EffectScatterChart, BarChart, LineChart } from 'echarts/charts';
import { UniversalTransition, LabelLayout } from 'echarts/features';
import { CanvasRenderer } from 'echarts/renderers';
import 'echarts/extension/bmap/bmap';
echarts.use([
    TitleComponent,
    TooltipComponent,
    ScatterChart,
    EffectScatterChart,
    CanvasRenderer,
    UniversalTransition,
    GridComponent,
    DatasetComponent,
    TransformComponent,
    BarChart,
    LabelLayout,
    // UniversalTransition,
    LineChart,
]);
var myChart = '';
var mycity = undefined
var mycity1 = undefined
var mycity2 = undefined
export default {
    // 基于准备好的dom，初始化echarts实例
    tt(dom, arr) {
        setTimeout(() => {//解决用rem为单位时，元素还没加载就已经执行
            if (myChart != null && myChart != "" && myChart != undefined) {
                myChart.dispose(); //销毁
            }
            myChart = echarts.init(document.getElementById(dom));
            // 绘制图表
            myChart.setOption({
                left: ".2rem",
                top: '.1rem',
                title: {
                    left: 'center',
                },
                tooltip: {
                    trigger: 'item'
                },
                visualMap: {
                    type: 'piecewise',
                    orient: 'horizontal',
                    itemWidth: 14,
                    selectedMode: false,
                    pieces: [
                        { min: 1000, max: 9000000, label: '>=10000', color: 'rgb(138, 46, 46)', show: false },
                        { min: 500, max: 999, label: '500-999', color: "rgb(172, 97, 97)" },
                        { min: 100, max: 499, label: '100-499', color: 'rgb(189, 118, 118)' },
                        { min: 10, max: 99, label: '10-99', color: 'rgb(233, 147, 147)' },
                        { min: 0, max: 9, label: '0-9', color: 'rgb(248, 221, 221)' },
                    ],
                    top: "91%",
                    textStyle: { width: 3 }
                },
                roamController: {
                    show: false,
                    left: 'right',
                    mapTypeControl: {
                        'china': true
                    }
                },
                // 每个数据
                series: [
                    {
                        name: '确诊数',
                        type: 'map',
                        map: 'china',
                        roam: false,
                        // center: [0, '30%'],
                        showLegendSymbol: false,
                        top: "0px",
                        bottom: "10%",
                        itemStyle: { borderWidth: ".1rem" },
                        label: {
                            show: true,
                            fontSize: ".1rem",
                            color: 'rgb(249, 249, 249)'
                        },

                        emphasis: {
                            itemStyle: {
                                areaColor: "#cbcbff"
                            },
                            label: {
                                fontSize: '.1rem',
                                color: 'rgb(249, 249, 249)'
                            }
                        },
                        select: {
                            itemStyle: {
                                areaColor: '#cbcbff',
                                color: 'rgb(249, 249, 249)',
                                shadowColor: 'rgba(10, 10, 10, 0.5)',
                                shadowBlur: 10
                            },
                            label: {
                                show: true,

                            }
                        },
                        tooltip: {
                            trigger: 'item',
                            backgroundColor: '#cbcbff',
                            borderWidth: "0",
                            axisPointer: {
                                type: 'shadow',
                                shadowStyle: {
                                    opacity: 0.4
                                },
                                label: {
                                    width: '.2rem',
                                    lineHeight: ".1rem",
                                    fontSize: ".2rem"
                                }
                            },
                            textStyle: {
                                fontSize: 1,
                                color: 'rgb(72, 72, 73)',
                            },
                        },
                        data: arr
                        //示例  [{name: '北京',value: 1}]
                    }
                ]
            })
            window.onresize = function () {
                myChart.resize();
            };
        }, 10)
    },
    top2_echarts(dom, val) {
        setTimeout(() => {
            if (myChart != null && myChart != "" && myChart != undefined) {
                myChart.dispose(); //销毁
            }
            myChart = echarts.init(document.getElementById(dom));
            myChart.setOption({
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                grid: {
                    top: "10%",
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: [
                    {
                        type: 'category',
                        data: ['累计确诊', '现确诊', '治愈', '死亡'],
                        axisTick: {
                            alignWithLabel: true
                        }
                    }
                ],
                yAxis: [
                    {
                        type: 'value'
                    }
                ],
                series: [
                    {
                        name: 'Direct',
                        type: 'bar',
                        barWidth: '30%',
                        data: val
                    }
                ]
            });
            window.onresize = function () {
                myChart.resize();
            };
        }, 10)
    },
    top2_city(dom, val, val1) {
        setTimeout(() => {
            if (mycity != null && mycity != "" && mycity != undefined) {
                mycity.dispose(); //销毁
            }
            mycity = echarts.init(document.getElementById(dom));
            mycity.setOption({
                title: {
                    text: '全球累计确诊人数',
                    left: "right",
                    textStyle: {
                        fontSize: ".2rem"
                    }
                },
                xAxis: {
                    type: 'category',
                    data: val,
                    axisLabel: {
                        fontSize: ".16rem",
                        interval: 0
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        fontSize: ".19rem",
                        interval: 0
                    }
                },
                grid: {
                    top: "20%",
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                series: [
                    {
                        data: val1,
                        type: 'line',
                    }
                ]
            });
            window.onresize = function () {
                mycity.resize();
            };
        }, 10)
    },
    top3_city(dom, val, val1) {
        setTimeout(() => {
            if (mycity1 != null && mycity != "" && mycity1 != undefined) {
                mycity1.dispose(); //销毁
            }
            mycity1 = echarts.init(document.getElementById(dom));
            mycity1.setOption({
                title: {
                    text: '全球累计治愈人数',
                    left: "right",
                    textStyle: {
                        fontSize: ".2rem"
                    }
                },
                xAxis: {
                    type: 'category',
                    data: val,
                    axisLabel: {
                        fontSize: ".16rem",
                        interval: 0
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        fontSize: ".19rem",
                        interval: 0
                    }
                },
                grid: {
                    top: "10%",
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                series: [
                    {
                        data: val1,
                        type: 'line',
                    }
                ]
            });
            window.onresize = function () {
                mycity1.resize();
            };
        }, 10)
    },
    top3_d(dom, val, val1) {
        setTimeout(() => {
            if (mycity2 != null && mycity != "" && mycity2 != undefined) {
                mycity2.dispose(); //销毁
            }
            mycity2 = echarts.init(document.getElementById(dom));
            mycity2.setOption({
                title: {
                    text: '全球疫情死亡人数',
                    left: "right",
                    textStyle: {
                        fontSize: ".2rem"
                    }
                },
                xAxis: {
                    type: 'category',
                    data: val,
                    axisLabel: {
                        fontSize: ".16rem",
                        interval: 0
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        fontSize: ".19rem",
                        interval: 0
                    }
                },
                grid: {
                    top: "10%",
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                series: [
                    {
                        data: val1,
                        type: 'line',
                    }
                ]
            });
            window.onresize = function () {
                mycity2.resize();
            };
        }, 10)
    },


}
