<template>
    <van-tabs type="card" @click="onClick" animated swipeable>
        <van-tab title="现确诊人数">
            <div class="alike">
                <div class="xqz" id="c1">
                    <!-- 现确诊人数地图位 -->
                    <div class="nav_right">
                        <span class="s1"></span>
                        <span class="s2"></span>
                        <span class="s3"></span>
                        <span class="s4"></span>
                        <span class="s5"></span>
                    </div>
                    <p>加载中.....</p>
                </div>
            </div>
        </van-tab>
        <van-tab title="治愈人数">
            <div class="alike zyr">
                <div class="xqz" id="c2">
                    <!-- 治愈人数地图位 -->
                    <div class="nav_right">
                        <span class="s1"></span>
                        <span class="s2"></span>
                        <span class="s3"></span>
                        <span class="s4"></span>
                        <span class="s5"></span>
                    </div>
                    <p>加载中.....</p>
                </div>
            </div>
        </van-tab>
    </van-tabs>
</template>
<script>
    // import chain from "./chain";
    import echarts from "../../Echarts/china"
    export default {
        data() {
            return {
                cure: 0
            }
        },
        mounted() {
            this.claer = setInterval(() => {
                //在app组件上申请疫情数据，当网洛较慢时还没返回数据就已经执行地图，导致数据错误
                //解决疫情数据还没申请到就开始渲染地图
                if (this.$store.state.time === 'cg1') {
                    clearInterval(this.claer);
                    echarts.tt('c1', this.$store.getters.qz_sum);
                    if (this.cure === 1) {//修复数据还没加载完成时直接点击tab第二个选项导致地图不出现问题
                        echarts.tt('c2', this.$store.getters.cureNum)
                    }
                }
            }, 100);
        },

        methods: {
            onClick(title) {
                if (title === 0) {
                    if (this.$store.state.time === 'cg1') {
                        this.$nextTick(() => {
                            echarts.tt('c1', this.$store.getters.qz_sum)
                        })
                    }
                }
                if (title === 1) {
                    if (this.$store.state.time === 'cg1') {
                        this.$nextTick(() => {
                            echarts.tt('c2', this.$store.getters.cureNum)
                        })
                    } else {
                        this.cure = 1;
                    }
                }
            }
        }
    }
</script>
<style scoped>
    .alike {
        position: relative;
        margin-top: .2rem;
        width: 100%;
        height: 6rem;
    }

    .xqz {
        margin: 0 auto;
        width: 7.2rem;
        height: 5rem;
        background-color: rgb(201, 241, 238);
    }

    :deep(.van-tabs__nav--card) {
        width: 4rem;
        height: .5rem;
        margin: .1rem .2rem;
        border: none;
    }

    :deep(.van-tabs__nav--card .van-tab.van-tab--active) {
        height: .5rem;
        background-color: aquamarine;
        color: rgb(50, 64, 252);
        box-shadow: 0 0 .1rem rgb(146, 146, 146);
    }

    :deep(.van-tabs__nav--card .van-tab) {
        font-size: .2rem;
        color: black;
    }

    :deep(.van-tabs__wrap) {
        height: 1rem !important;
    }

    :deep(.van-tabs__nav--card .van-tab) {
        border: .01rem rgb(133, 183, 248) solid;
        margin-right: .1rem;
    }

    :deep(.van-tabs__nav--card .van-tab:last-child) {
        border-right: .01rem rgb(133, 183, 248) solid;
    }

    /*  等待动画*/
    .nav_right {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1.5rem;
    }

    .nav_right>span {
        display: inline-block;
        margin-right: .2rem;
        width: .1rem;
        height: .6rem;
        background-color: rgb(241, 240, 240);
        border-radius: 50%/0.1rem;
    }

    .s1 {
        animation: atguigu 0.5s linear 0.1s infinite;
    }

    .s2 {
        animation: atguigu 0.5s linear 0.3s infinite;
    }

    .s3 {
        animation: atguigu 0.5s linear 0.6s infinite;
    }

    .s4 {
        animation: atguigu 0.5s linear 0.9s infinite;
    }

    .s5 {
        animation: atguigu 0.5s linear 1.3s infinite;
    }

    @keyframes atguigu {
        0% {
            transform: scaleY(1);
            background-color: aqua;
        }

        50% {
            transform: scaleY(.5);
            background-color: rgb(234, 0, 255);
        }

        100% {
            transform: scaleY(1);
            background-color: rgb(72, 255, 0);
        }
    }
</style>