<template>
    <div class="toplist">
        <div @click="diqu">
            <p style="padding-left:.1rem">
                <span :style="{'display':dn,'visibility':vn}" class="sp" :class="spa"></span>
                <span class="centr">{{vis(valname[0])}}</span>
            </p>
            <p>{{valname[1]}}</p>
            <p>{{valname[2]}}</p>
            <p>{{valname[3]}}</p>
            <p>{{valname[4]}}</p>
        </div>
        <div class="toplist_disp" v-if="n_y" v-for="(val,index) in arrcity" :key="index">
            <p>{{val.cityName}}</p>
            <p>{{arrcity_val(val.confirmedNum)}}</p>
            <p>待统计</p>
            <p>{{arrcity_val(val.curedNum)}}</p>
            <p>{{arrcity_val(val.deadNum)}}</p>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                n_y: false,
                spa: '',
                vn: 'inherit'
            }
        },
        methods: {
            diqu() {
                this.n_y = this.n_y === false ? true : false;
                this.spa = this.spa === '' ? "sp_dh" : '';
            },
            arrcity_val(val) {
                return val === '' ? 0 : val
            },
            vis(val) {
                if (val === '台湾' || val === '香港' || val === '澳门') {
                    this.vn = "hidden"
                }
                return val
            }
        },
        props: {
            valname: {
                required: true, //name是必要的
            },
            arrcity: {
                required: true
            },
            dn: {
                default: 'inline-block'
            }
        }
    }
</script>
<style scoped>
    .toplist {
        width: 100%;
    }

    .toplist>div {
        display: table;
        width: 100%;
        height: .8rem;
    }

    .toplist>div:nth-child(1) {
        background-color: rgb(240, 243, 245);
    }

    .toplist>div>p:nth-child(1) {
        text-align: left;
    }

    .sp {
        display: inline-block;
        width: 0rem;
        height: 0rem;
        border: .1rem solid rgb(55, 0, 255);
        border-right: .1rem solid rgb(255, 0, 0, 0);
        border-top: .1rem solid rgb(255, 0, 0, 0);
        border-bottom: .1rem solid rgb(255, 0, 0, 0);
        margin-right: -.001rem;
        margin-left: .01rem;
        transition: 0.2s linear 0s;
    }

    .sp_dh {
        transform: rotate(90deg);
    }

    .toplist>div>p {
        display: table-cell;
        width: 1rem;
        height: 100%;
        text-align: center;
        vertical-align: middle;
    }

    .toplist>div>p+p {
        border-left: .001rem solid whitesmoke;
    }

    .toplist_disp>p {
        width: 1rem;
    }

    .toplist_disp>p:nth-child(1) {
        padding-left: .1rem;
    }

    .centr {
        display: inline-block;
        width: 75%;
        vertical-align: middle
    }
</style>