import vue from 'vue';
import vuerouter from 'vue-router';
vue.use(vuerouter);
import home from "../pages/home"//主组件
import navigation from "../pages/navs/navigation"//功能指南
import supplies from "../pages/navs/supplies"
import top from "../pages/situation/top"//地区top
import top1 from "../pages/situation/top1"
import top2 from "../pages/situation/top2"
import top3 from "../pages/situation/top3"
import top4 from "../pages/situation/top4"
import guide from "../pages/navs/guide"
import phoe from "../pages/navs/phoe"
export default new vuerouter({
    routes: [
        {
            path: '/',//页面加载完成就执行，无需通过<router to>调用
            component: home,
            children: [
                {
                    path: '/',
                    component: top,
                    children: [
                        {
                            path: '/',
                            component: top1,
                        },
                        {
                            path: 'top2',
                            name: 'top2',
                            component: top2,
                        },
                        {
                            path: 'top3',
                            name: 'top3',
                            component: top3,
                        },
                        {
                            path: 'top4',
                            name: 'top4',
                            component: top4,
                        },
                    ]
                }
            ]
        },
        {
            path: '/navigation',
            component: navigation,
            props($route) {
                return {
                    tit: $route.query.tit
                }
            }
        },
        {
            path: '/supplies',
            component: supplies,
        },
        {
            path: '/guide',
            component: guide,
        },
        {
            path: '/phoe',
            component: phoe,
        }

    ]
})