import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const actions = {}
const mutations = {
    high(state, val) {
        state.highDangerCount = val[0]
        state.midDangerCount = val[1]
    },
    yqname(state, val) {
        state.yqname_list = []
        state.yqname_list.push(...val)
    },
    data_domestic(state, val) {
        state.domestic = val
    },
    world(state, val) {
        state.world = val
    },
    time_data(state, val) {
        state.timedata = val
    }
}
const state = {
    // 数据返回
    time: '',
    // 风险区高/中
    highDangerCount: '',
    midDangerCount: '',
    //疫情数据更新时间
    times: "",
    // 国内疫情区数据
    yqname_list: [],
    //确诊人数/治愈人数
    num: [],
    //国内外现确诊，累计等数据对象
    domestic: {},
    //获取世界数据
    world: undefined,
    //获取世界前八天数据
    timedata: []
}
const getters = {
    //各省现确诊人数
    qz_sum(state) {
        let yqNameNumber = []
        for (var a = 0; a < state.yqname_list.length; a++) {
            let name = state.yqname_list[a].provinceName
            let arr_name = name.split("")
            let an = arr_name.filter((val) => {
                return val !== '省' && val !== '市' && val !== '自' && val !== '治' && val !== '区' && val !== '壮' && val !== '族' && val !== '回' && val !== '维' && val !== '吾' && val !== '尔'
            })
            let an1 = an.join('')
            let number = state.yqname_list[a].currentConfirmedNum
            let bb = `{"name":"${an1}","value":${number}}`
            yqNameNumber.push(JSON.parse(bb));
        }
        return yqNameNumber;
    },
    //各省治愈人数
    cureNum(state) {
        let cureNum = []
        for (var a = 0; a < state.yqname_list.length; a++) {
            let name = state.yqname_list[a].provinceName
            let arr_name = name.split("")
            let an = arr_name.filter((val) => {
                return val !== '省' && val !== '市' && val !== '自' && val !== '治' && val !== '区' && val !== '壮' && val !== '族' && val !== '回' && val !== '维' && val !== '吾' && val !== '尔'
            })
            let an1 = an.join('')
            let number = state.yqname_list[a].curedNum
            let bb = `{"name":"${an1}","value":"${number}"}`
            cureNum.push(JSON.parse(bb));
        }
        return cureNum;
    },
    // 某个省的数据
    certo(state, val) {
        console.log(val);
    }
}
export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})